import type { Metadata } from 'next';
import { Inter } from 'next/font/google';
import Link from 'next/link';
import './globals.css';
import { NextAuthProvider } from '@/providers/NextAuthProvider';

const inter = Inter({ subsets: ['latin'] });

export const metadata: Metadata = {
  title: 'Agro E-Commerce',
  description: 'Your one-stop shop for agricultural products',
};

export default function RootLayout({
  children,
}: {
  children: React.ReactNode;
}) {
  return (
    <html lang="en">
      <body className={inter.className}>
        <header className="bg-primary text-white">
          <nav className="container mx-auto px-4 py-4">
            <div className="flex items-center justify-between">
              <h1 className="text-2xl font-bold">AgroEcommerce</h1>
              <div className="space-x-4">
                <Link href="/" className="hover:text-secondary">Home</Link>
                <Link href="/products" className="hover:text-secondary">Products</Link>
                <Link href="/cart" className="hover:text-secondary">Cart</Link>
                <Link href="/dashboard" className="hover:text-secondary">Dashboard</Link>
                <Link href="/signin" className="hover:text-secondary">Sign In</Link>
                <Link href="/signup" className="hover:text-secondary">Sign Up</Link>
                <Link href="/test" className="hover:text-secondary">Test</Link>
              </div>
            </div>
          </nav>
        </header>
        <main className="container mx-auto px-4 py-8">
          <NextAuthProvider>{children}</NextAuthProvider>
        </main>
        <footer className="bg-gray-100 mt-8">
          <div className="container mx-auto px-4 py-6">
            <p className="text-center text-gray-600">
              Â© 2024 AgroEcommerce. All rights reserved.
            </p>
          </div>
        </footer>
      </body>
    </html>
  );
} 